import React from 'react';
import Col from 'react-bootstrap/Col';

export class TvShow extends React.Component {
    render() {
        let image = {
            src: this.props.src,
            alt: this.props.alt,
            title: this.props.title
        };
        return (
            <Col lg="2">
                <img className="img-fluid" src={image.src} alt={image.alt} title={image.title} />
            </Col>
        );
    }
}